.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
  float: left;
}

.App-header {
  background-color: #282c34;
  color: white;
  height: 50px;
}

.App-title {
  font-size: 20px;
  font-weight: 600;
  color: #61dafb;
  float: left;
  padding-top: 10px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main{
    margin-top: 40px;
}
.register{
    width: 500px;
    margin-left: auto;
    margin-right: auto;
}
.login-form{
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}

.login-form-button{
    width: 100%;
}
.logout {
    float: right;
    padding-top: 13px;
    padding-right: 13px;
}

.home {
    margin-left: 100px;
    margin-right: 100px;
}

.video-block {
    height: 400px;
    width: 220px;
}

.topic-radio-group {
    margin-bottom: 20px;
}

.around-marker-image {
    height: 120px;
    width: 150px;
}

.around-marker-video {
    height: 300px;
    width: 180px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

